<template>
    <div v-if="loading" class="d-flex justify-content-center mb-5">
        <div class="spinner-border spinner-border-sm text-danger"></div>
    </div>
    <div v-else>
        <Breadcrumb :program="program" />
        <section id="select-time">
            <div class="container">
                <Cover :program="program" />
                <BuyProcess :routeName="routeName" />
                <div class="select-time">
                    <ul class="select-time__text-list nav flex-column mb-5">
                        <h3 class="select-time--header">انتخاب سانس</h3>
                        <li class="select-time__text-item">لطفا سانس اجرا را انتخاب کنید</li>
                        <li class="select-time__text-item">لطفا در انتخاب صندلی دقت فرمایید، پس از اتمام خرید امکان
                            جابجایی یا کنسلی وجود ندارد.</li>
                        <li class="select-time__text-item">جهت ورود به سالن، رعایت حجاب اسلامی الزامی میباشد.</li>
                    </ul>
                    <ul class="select-time__list nav justify-content-sm-between justify-content-center">
                        <li v-for="(san, k) in sans" :key="k" :class="{
                            'select-time__item': true,
                            'select-time__item--sold-out': san.status === 'sold_out' || san.status === 'held'
                        }">
                            <p class="select-time__item--date">{{ san.start_date.split(' ')[0] }}</p>
                            <p class="select-time__item--time">{{ san.start_date.split(' ')[1] }}</p>
                            <router-link :to="{
                                name: 'seats',
                                params: {
                                    programId: program.id,
                                    sanId: san.id
                                }

                            }" :class="{
                                'select-time__item--btn': true,
                                btn: true,
                                disabled: ['sold_out', 'held'].includes(san.status)
                            }">
                                {{ san.status === 'sold_out' || san.status === 'held' ? 'اتمام' : 'انتخاب' }}
                            </router-link>
                        </li>
                    </ul>
                </div>
            </div>
        </section>
    </div>
</template>
<script setup>
import { ref, onMounted, computed } from 'vue';
import { useRoute } from 'vue-router';
import Api from '@/axios/api'
import Cover from './Cover.vue'
import { useHead } from '@vueuse/head'
import Breadcrumb from '../Breadcrumb.vue';
import BuyProcess from '../BuyProcess.vue';
import { handleError } from '@/utils/helpers';
let loading = ref(false)
const route = useRoute();
const sans = ref([]);
let program = ref('');
onMounted(async () => {
    try {
        // loading.value = true
        const url = `/programs/${route.params.id}/select-time`;
        const response = await Api.get(url);

        if (response.data.status == 'success') {
            sans.value = response.data.data.sans
            program.value = response.data.data.program
            useHead({
                title: computed(() => `${program.value.title} - ${program.value.artist.name} ${program.value.artist.family}`),
                meta: [
                    {
                        name: `description`,
                        content: computed(() => {
                            return program.value.title + " " + program.value.hall.title + " " + program.value.hall.city.title
                        }),
                    },
                ],

            })
        }
    } catch (err) {
        toast.error(handleError(err).join(', '), {
            timeout: 3000
        });
    } finally {
        loading.value = false
        // window.scrollTo({ top: 0, behavior: 'smooth' });
    }
})
</script>
